<template>
    <div>

        <van-nav-bar title="专利详情" left-text="返回" left-arrow @click-left="back">
            <van-icon name="search" slot="right"/>
        </van-nav-bar>
        <demo-block title="123">

        </demo-block>
        <van-panel>
            <div slot="header">
                <van-cell :title="pn" :label="TI" class="author-name"/>
            </div>
            <van-cell-group>

                <van-cell icon="award-o">
                    <template slot="title">
                        <span class="custom-text">专利画像</span>
                        <van-tag plain type="primary" class="ttaagg" v-show="gby">高被引专利</van-tag>
                    </template>
                </van-cell>


                <van-cell title="专利摘要" :label="AB"/>
                <van-cell title="专利作者" :label="AU"/>
                <van-cell title="专利权人" :label="AE"/>
                <van-cell title="专利分类" :label="DC"/>
                <van-cell title="专利引用情况" :label="CP"/>


            </van-cell-group>

        </van-panel>

    </div>
</template>

<script>
    import {
        Checkbox,
        CheckboxGroup,
        Card,
        SubmitBar,
        Toast,
        NavBar,
        Tag,
        Pagination,
        Panel,
        Cell,
        CellGroup,
        Button,
        Progress,
        Collapse,
        CollapseItem,
        Rate

    } from 'vant';
    import axios from 'axios';

    export default {
        components: {
            [Card.name]: Card,
            [Checkbox.name]: Checkbox,
            [SubmitBar.name]: SubmitBar,
            [CheckboxGroup.name]: CheckboxGroup,
            [NavBar.name]: NavBar,
            [Tag.name]: Tag,
            [Pagination.name]: Pagination,
            [Panel.name]: Panel,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [Button.name]: Button,
            [Progress.name]: Progress,
            [Collapse.name]: Collapse,
            [CollapseItem.name]: CollapseItem,
            [Rate.name]: Rate,
        },

        data() {
            return {
                activeNames: ['-1'],
            };
        },


        methods: {

            onInit() {
                this.pn = this.$route.params.pn;
                this.tag = this.$route.params.name;


                const path = `api/zhuanlidetail/` + this.pn;
                axios.get(path)
                    .then(response => {
                        this.pn = response.data.PN
                        this.CitedTimes = response.data.CitedTimes
                        this.AB = response.data.AB
                        this.TI = response.data.TI
                        this.AE = response.data.AE
                        this.AU = response.data.AU
                        this.DC = response.data.DC
                        this.CP = response.data.CP

                        if (this.CitedTimes >= 1) {
                            this.gby = true
                        } else {
                            this.gby = false
                        }

                        this.$forceUpdate()
                    })
                    .catch(error => {
                        console.log(error)
                    })
                this.$nexttick(() => {
                    window.scrollTo(0, 1)
                    window.scrollTo(0, 0)
                })
            },

            back() {
                if (this.tag == undefined) {
                    this.$router.push('../zhuanli');

                }else {
                    this.$router.push('/person/' + this.tag);
                }

            }
        },

        created() {
            this.onInit();
        },
    };
</script>
<style lang="less">
    .author-name {
        font-size: 24px;
    }

    .ttaagg {
        margin-left: 5px;
    }
</style>